html,
body {
  text-align: center;
  color: var(--bg-accent);
  background-color: var(--bg-color);
  margin: 0;
  padding: 0;
  font-family: sans-serif !important;
  font-size: 20px !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
}
body {
  overflow-y: scroll;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
  color: inherit;
}

:root {
  --primary-color: #512da8;
  --primary-light: #7f57c2;
  --primary-dark: #321b92;
  --primary-accent: #ffffff;
  --complementary-color: #2d46a8;
  --complementary-accent: #ffffff;
  --bg-color: #fff;
  --bg-accent: #000;
  --gray: #999999;
  --light-gray: #eeeeee;
  --dark-gray: #222222;

  --input-padding: 8px 16px;
  --input-line-height: 16px;
}

.navbar {
  height: 5rem;
  background-color: var(--primary-color);
  background: linear-gradient(
    to right,
    var(--primary-color),
    var(--complementary-color)
  );
  text-align: left;
  box-shadow: 0px 0px 10px 1px var(--primary-dark);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  right: 0;
}

.navbar > a {
  display: grid;
  place-items: center;
  grid-auto-flow: column;
  color: var(--primary-accent);
  text-decoration: none;
  background-color: transparent;
  margin: 0rem 2rem;
  padding: 0 1rem;
  transition: 250ms ease-in-out background-color, 350ms ease-out box-shadow;
  border-radius: 25px 25px 0 0;
  height: 100%;
  float: left;
}
.navbar > a.right {
  float: right;
}

.navbar > a.active {
  background-color: white;
  color: var(--primary-color);
  box-shadow: 0px 0px 5px 0px var(--primary-accent),
    0px 1px 1px 0px var(--primary-light);
}
.navbar > a.active:hover {
  filter: none;
  cursor: default;
}
.navbar > a:hover {
  filter: brightness(0.8);
}

.navbar > .search-container {
  color: var(--bg-accent);

  margin: 1.6rem 0;
  width: fit-content;
  float: left;
  overflow: clip;
  padding: 0;
}
.navbar > .search-container input {
  box-sizing: border-box;
  margin: 0;
  color: var(--bg-accent);
  width: 16rem;
  padding: 0;
  border: 1px var(--primary-dark) solid;
  display: block;
  height: 1.8rem;
  border-radius: 10px 10px 0px 0px;
  font-size: 1rem;
  padding: 0.3rem;
  outline: none;
}

.navbar > .search-container:not(:focus-within),
.navbar > .search-container.empty input {
  border-radius: 10px;
}

.navbar > .search-container input:not(:focus) + ul:not(:active) {
  display: none;
}

.navbar > .search-container ul {
  box-sizing: border-box;
  background-color: var(--bg-color);
  list-style: none;
  padding: 0;
  border: 1px var(--primary-dark) solid;
  border-top: none;
  margin: 0;
  width: 16rem;
  border-radius: 0px 0px 10px 10px;
  display: block;
}

.navbar > .search-container.empty ul {
  display: none;
}

.navbar > .search-container ul li {
  overflow: hidden;
  padding: 0.1rem 0.3rem;
}

.navbar > .search-container ul li:nth-child(2n) {
  background-color: var(--light-gray);
}

.navbar > .search-container ul li:hover {
  background-color: var(--complementary-color);
  color: var(--complementary-accent);
  filter: none;
}

.navbar > .search-container ul li a {
  width: 100%;
  height: 100%;
  display: block;
}

.navbar ~ * {
  margin-top: 6rem;
}

#register_account h1,
#login_account h1 {
  display: inline-block;
  color: var(--primary-dark);
  margin: 10px;
}

form {
  /* 
  border: 1px var(--primary-light) solid; */
  margin: auto;
  width: fit-content;
  padding: 50px;
  border-radius: 10px;
  box-shadow: 0px 5px 10px -5px var(--primary-color);
}

.input-group {
  display: inline-block;
  width: 360px;
  position: relative;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  margin: 10px;
}
.input-group input {
  box-sizing: border-box;
  padding: var(--input-padding);
  border: 3px solid var(--gray);
  background-color: white !important;
  border-radius: 5px;
  width: 100%;
  color: var(--gray);
  font-size: 16px;
  line-height: var(--input-line-height);
  outline: none;
  text-align: left;
  transition: 300ms ease-in-out all;
}

.input-group input:-webkit-autofill,
.input-group input:-webkit-autofill:hover,
.input-group input:-webkit-autofill:focus,
.input-group input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 40px white inset !important;
  box-shadow: 0 0 0 40px white inset !important;
  transition: 0ms linear -webkit-box-shadow;
  color: var(--gray);
}

.input-group:hover input {
  border-color: var(--primary-light);
}
.input-group input:focus {
  border-color: var(--primary-color);
}

.input-group input:focus,
.input-group:hover input {
  color: var(--primary-color);
}

.input-group label {
  position: absolute;
  background-color: transparent;
  padding: var(--input-padding);
  line-height: var(--input-line-height);
  top: 0;
  left: 0;
  bottom: 0;
  color: var(--gray);
  cursor: text;
  transition: 300ms ease-in-out all;
}

.input-group:hover label {
  color: var(--primary-color);
}

.input-group:focus-within label,
.input-group input:not([value=""]) ~ label {
  padding: 0px 8px;
  background-color: var(--primary-accent);
  height: 5px;
  top: 0px;
  left: 16px;
  bottom: auto;
  line-height: 5px;
  color: var(--primary-light);
} /* 
.input-group input:not([value=""]) {
  border: 3px solid var(--primary-color);
}
 */

button {
  cursor: pointer;
  position: relative;
  display: inline-block;
  padding: 12px 36px;
  margin: 10px 0;
  color: var(--primary-accent);
  text-decoration: none;
  text-transform: uppercase;
  font-size: 18px;
  letter-spacing: 2px;
  border-radius: 40px;
  background: linear-gradient(
    90deg,
    var(--primary-color),
    var(--complementary-color)
  );
  border: none;
  overflow: hidden;
  box-shadow: 0 0 20px 0px var(--primary-color);
  transition: 200ms ease-out filter;
  transition: 200ms ease-out box-shadow;
}

button:hover {
  filter: brightness(1.1);
  box-shadow: 0 0 20px 0px var(--primary-light);
}

button:disabled {
  cursor: default;
  opacity: 0.38;
  box-shadow: none;
  background: var(--gray);
  color: var(--dark-gray);
  font-weight: bold;
}
button:disabled:hover {
  filter: none;
  box-shadow: none;
}

button span {
  position: absolute;
  background: #fff;
  transform: translate(-50%, -50%);
  pointer-events: none;
  border-radius: 50%;
  animation: buttonCircleAnimation 1s linear infinite;
}

@keyframes buttonCircleAnimation {
  0% {
    width: 0px;
    height: 0px;
    opacity: 0.5;
  }
  100% {
    width: 500px;
    height: 500px;
    opacity: 0;
  }
}
.field-validation-error {
  border: 1px hsl(0, 91%, 70%) solid;
  box-sizing: border-box;
  font-size: 14px;
  color: #bb0909;
  background-color: white;
  opacity: 0.9;
  max-width: 360px;
  margin: auto;
  padding: 20px;
  border-bottom: 10px solid hsl(0, 91%, 60%);
  display: inline-block;
}

#create {
  margin-top: 6rem;
}
#create form {
  margin: 50px auto;
}

#login_account,
#register_account {
  min-height: 100vh;
  display: grid;
  place-items: center;
  grid-template-rows: min-content auto 10vh;
}

#profile .profile-container {
  display: grid;
  grid-template-columns: 12rem 1fr;
}

#profile .profile-container .picture {
  width: 6rem;
  height: 6rem;
  border: 1px black solid;
  box-shadow: 0 0 5px 0 var(--primary-color);
  overflow: hidden;
  line-height: 6rem;
  border-radius: 50%;
  margin: 1rem auto;
}
#profile .profile-container .picture img {
  width: 6rem;
  text-align: center;
  vertical-align: middle;
}
#profile .profile-container .username {
  font-size: 50px;
  border-left: 1px black solid;
  line-height: 8rem;
}

#myposts {
  padding: 1rem 4rem;
  border-top: 1px black solid;
}

#myposts .post {
  display: inline-block;
  height: 12rem;
  width: 6rem;
  overflow: hidden;
  margin: 30px 20px;
  box-shadow: 0px 5px 10px -3px var(--primary-color);
  padding: 0.5rem 2rem;
  border-radius: 5%;
}

#myposts img {
  width: 70%;
  box-shadow: 0px 5px 10px -5px var(--primary-color);
}

.navbar a.nav-profile {
  margin-left: 0;
}

.navbar a.nav-profile span {
  margin: 0 0.5rem 0 0;
}

.navbar a.nav-profile.active img {
  box-shadow: 0px 0px 0px 0.1rem var(--primary-dark);
}
.navbar a.nav-profile img {
  background-color: var(--complementary-color);
  box-shadow: 0px 0px 0px 0.1rem white;
}

.navbar a img {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  vertical-align: middle;
}
